import toastr from '../components/notify';

export default function animateEmojis() {
  document.querySelectorAll('.emoji').forEach(element => {
    element.addEventListener('click', () => {
      document.querySelectorAll('.emoji.active').forEach(active => active.classList.remove('active'));
      requestAnimationFrame(() => element.classList.add('active'));
      toastr.success('–í–∞—à–µ –≤—Ä–∞–∂–µ–Ω–Ω—è –≤—Ä–∞—Ö–æ–≤–∞–Ω–æ');
    });
  });
}
